.md-bottom-navigation {
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  overflow: hidden;
  position: fixed;
  width: 100%;
  z-index: 11;
}

.md-bottom-navigation--shifting {
  transition-duration: 0.3s;
  transition-property: background;
}

.md-bottom-navigation--dynamic {
  transform: translate3d(0, 0, 0);
  transition-duration: 0.3s;
  transition-property: background, transform;
}

.md-bottom-navigation--dynamic-inactive {
  transform: translate3d(0, 100%, 0);
}

.md-bottom-navigation-offset {
  padding-bottom: 56px;
}

.md-bottom-nav {
  display: block;
  flex-grow: 1;
  font-size: 12px;
  height: 56px;
  max-width: 168px;
  padding: 8px 12px 10px;
  text-align: center;
  text-decoration: none;
  user-select: none;
}

.md-bottom-nav--active {
  flex-shrink: 0;
  font-size: 14px;
  padding-top: 6px;
}

.md-bottom-nav--fixed {
  min-width: 80px;
}

.md-bottom-nav--shifting {
  min-width: 96px;
  position: initial;
  transition-duration: 0.15s;
  transition-property: max-width;
}

.md-bottom-nav--shifting-inactive {
  max-width: 96px;
  min-width: 56px;
  padding-top: 16px;
}

.md-bottom-nav--shifting .md-ink-container {
  overflow: visible;
}

.md-bottom-nav--shifting .md-ink {
  background: rgba(255, 255, 255, 0.12);
}

.md-bottom-nav-label {
  transition-duration: 0.15s;
  transition-property: color, font-size;
}

.md-bottom-nav-label--shifting-inactive {
  max-width: 32px;
  overflow: hidden;
  white-space: nowrap;
